import React from "react";
import { Link } from "react-router-dom";
import Darkreader from "react-darkreader";

import { Menu } from "antd";
import { HomeOutlined, UserOutlined, FolderOutlined } from "@ant-design/icons";
import "./Nav.css";
import Logo from "../../logo.png";

export default class Nav extends React.Component {
  state = {
    current: "home",
    items: [
      {
        label: <Link to="/"></Link>,
        key: "home",
        icon: <HomeOutlined />,
      },
      {
        label: <Link to="vued">Vue</Link>,
        key: "vued",
        icon: <FolderOutlined />,
      },
      {
        label: <Link to="reactd">React</Link>,
        key: "reactd",
        icon: <FolderOutlined />,
      },
      {
        label: <Link to="other"></Link>,
        key: "other",
        icon: <FolderOutlined />,
      },
      {
        label: <Link to="about"></Link>,
        key: "about",
        icon: <UserOutlined />,
      },
    ],
  };

  componentDidMount() {
    //
    //
    const userAgent = navigator.userAgent;
    if (
      userAgent.match(/(iPhone|iPod|Android|ios|iPad|AppleWebKit.*Mobile.*)/i)
    ) {
      console.log("");
      this.setState({ current: false });
    } else {
      console.log("PC");
      this.setState({ current: true });
    }
  }
  onClick = (e) => {
    console.log("click ", e);
    this.setState({ current: e.key });
  };

  render() {
    const { current, items } = this.state;
    return (
      <div className="header">
        <div className="imgdiv">
          <img className="logo" src={Logo} alt="logo" />
        </div>
        <Darkreader />
        <Menu
          className="menu"
          inlineCollapsed={false}
          onClick={() => {
            this.onClick();
          }}
          selectedKeys={[current]}
          mode="horizontal"
          items={items}
        />
      </div>
    );
  }
}
